
import React from 'react';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';
import Login from './Login';
import Welcome from "./Welcome";
import Development from "./AppDevelopment";
import ProtectedRoute from './ProtectedRoute';
import { setBasePath } from '@shoelace-style/shoelace/dist/utilities/base-path';


//import SlCard from '@shoelace-style/shoelace/dist/react/card';
import Shell from "./Shell.js";
//import Currentprojects from "./Currentprojects.js";
//import AboutUs from "./Aboutus.js";

setBasePath('https://cdn.jsdelivr.net/npm/@shoelace-style/shoelace@2.15.0/cdn/');

function App() {
  return (

    <BrowserRouter>
      <Routes>
        <Route path="/" element={<Welcome />} />
        <Route path="/propertymanagement" element={<ProtectedRoute><Shell /></ProtectedRoute>} />
        <Route path="/propertydevelopment" element={<Development />} />
        <Route path="*" element={<Navigate to="/" />} />
        <Route path="/login" element={<Login />} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
