import SlTab from '@shoelace-style/shoelace/dist/react/tab';
import SlTabGroup from '@shoelace-style/shoelace/dist/react/tab-group';
import SlTabPanel from '@shoelace-style/shoelace/dist/react/tab-panel';
import SlButton from '@shoelace-style/shoelace/dist/react/button';
import SlDialog from '@shoelace-style/shoelace/dist/react/dialog';
import { Navigate } from 'react-router-dom';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
//import SlCard from '@shoelace-style/shoelace/dist/react/card';
import Home from "./Home";
import OverviewFix from "./OverviewFix";
import CreateNewFix from "./CreateNewFix";
import Calendar from "./Calendar";
//import Currentprojects from "./Currentprojects.js";
//import AboutUs from "./Aboutus.js";

const css = `
  .app {
    text-align: center;
    padding: 1vw;

    sl-button {
        margin: 0px 5px;
    }
  }

  .app-header {
    text-align: right;
    border-bottom: 1px solid lightgray;
  }

  .app-header > img {
    width: 188px;
  }

  sl-tab-group {
    /* sl-button {
      margin: 13px 0px;
    } */
  }

  sl-tab-group::part(tabs) {
    --track-width: 5px;
  }

  sl-tab-panel::part(base) {
   padding: 50px;
  }

  .card-image-profile {
    min-width: 200px;
    padding: 20px;
    max-width: 250px;
    border: 2px solid var(--sl-color-gray-300);
    margin: 1vw;
  }


  sl-tab {
    text-align: left;
    width: 200px;
  }

  sl-tab-group::part(nav) {
  border-inline-end: solid var(--track-width) var(--track-color);
  }

  .leftnav {
      display: flex;
      flex-direction: column;
      

      sl-button {
          width: 200px;
          margin: 12px 0px;
      }
  }
`;


function Shell() {
    const [data, setData] = useState([])

    //Overlay
    const [open, setOpen] = useState(false);
    const fetchTasks = async () => {
        try {
            const response = await axios.get('http://localhost:5000/api/tasks'); // Adjust port if needed
            console.log(response)
            setData(response.data);
        } catch (error) {
            console.error('Error fetching items:', error);
        }
    };

    //This will go to Global Store
    useEffect(() => {
        fetchTasks();         
      }, []);

    const [input, setInputs] = useState({
        title: "",
        description: "",
        recurring: "",
        date: new Date().toLocaleDateString('en-US'),
        unit: ""
      });
      
      const addTask = async () => {
        try {
          await axios.post('http://localhost:5000/api/tasks', input);
          setInputs({
            title: "",
            description: "",
            recurring: "",
            date: new Date().toLocaleDateString('en-US'),
            unit: ""
          })
          // Refresh the list. This will showup in OverviewFix, but after AI runs
          // For now just show it in the table.
          fetchTasks(); 
          setOpen(false)

        } catch (error) {
          console.error('Error adding item:', error);
        }
      };


    //Global element
    const [authenticated, setAuthenticated] = useState(true);

  const clickTabSignOff = (e) => {
    console.log(e)
    localStorage.removeItem('authToken'); 
    setAuthenticated(false)
  }
  const closeDialog = () => {
    setInputs({
        title: "",
        description: "",
        recurring: "",
        date: new Date().toLocaleDateString('en-US'),
        unit: ""
      })
    setOpen(false)
  }
  return (
    <div className="app">
      <SlDialog 
        label="New Repair" 
        open={open} 
        onSlAfterHide={closeDialog}
    >
        <div
          style={{
            //height: '150vh',
            //border: 'dashed 2px var(--sl-color-neutral-200)',
            padding: '0 1rem'
          }}
        >
          <CreateNewFix  
            input={input} 
            setInputs={setInputs}
          />
        </div>

        <SlButton slot="footer" variant="default" onClick={() => closeDialog}>
          Cancel
        </SlButton>
        <SlButton slot="footer" variant="primary" onClick={addTask}>
          Submit
        </SlButton>
      </SlDialog>
      <header className="app-header">
         <img
            slot="image"
            src="./images/LogoSerale.png"
            alt="JADU."
          />
      </header>
      {!authenticated && <Navigate to="/login" replace />}
      {authenticated && 
      <div className="leftnav">
         <SlButton variant="primary" onClick={() => setOpen(true)}>
          Create New Task
        </SlButton>
      <SlTabGroup placement="start">
        <SlTab slot="nav" panel="tab-2" active >
          Overview Tasks
        </SlTab>
        <SlTab slot="nav" panel="tab-4">
          Calendar
        </SlTab>
        <SlTab slot="nav" panel="tab-5">
          Reports
        </SlTab>
        <SlTab slot="nav" panel="tab-6">
          Notifications
        </SlTab>
        <SlTab slot="nav" panel="tab-7">
          Help FAQ
        </SlTab>
        <SlTab slot="nav" panel="tab-8">
          Settings
        </SlTab>
        <SlTab slot="nav" panel="tab-9" onClick={clickTabSignOff}>
          Sign Off
        </SlTab>

        <SlTabPanel name="tab-2">
          <OverviewFix data={data}/>
        </SlTabPanel>
        <SlTabPanel name="tab-4">
          <Calendar />
        </SlTabPanel>
        <SlTabPanel name="tab-5">
          <Home />
        </SlTabPanel>
        <SlTabPanel name="tab-6">
          <Home />
        </SlTabPanel>
        <SlTabPanel name="tab-7">
          <Home />
        </SlTabPanel>
        <SlTabPanel name="tab-8">
          <Home />
        </SlTabPanel>
        <SlTabPanel name="tab-9">
          <Home />
        </SlTabPanel>
      </SlTabGroup>
     </div>}
      
      <style>{css}</style>
    </div>
  )
}


export default Shell;